window.dom = {};

/**
 * Ê†πÊçÆÂèÇÊï∞ÂàõÈÄ†Êñ∞ÁöÑËäÇÁÇπ
 * dom.create('<div>Áõ¥Êé•Ëøô‰πàÂàõÂª∫</div>');
 * @param {string} tagNameString Ê†áÁ≠æÂêç
 */
dom.create = function (tagNameString) {

    const container = document.createElement('template');
    container.innerHTML = tagNameString.trim();
    return container.content.firstChild;

};

/**
 * Âú®nodeËäÇÁÇπÂêéÈù¢Ê∑ªÂä†ËäÇÁÇπnewNode
 * @param {Object} node 
 * @param {Object} newNode Êñ∞ÁöÑËäÇÁÇπ
 */
dom.after = function (node, newNode) {
    let parent = node.parentNode;
    // parentNode.insertBefore(newNode, referenceNode); ËøîÂõûÁöÑÂÄºÊòØnewNode 
    // node.nextSibling nodeËäÇÁÇπÁöÑ‰∏ã‰∏Ä‰Ωç
    return parent.insertBefore(newNode, node.nextSibling);
}

/**
 * Âú®nodeËäÇÁÇπÂâçÈù¢Ê∑ªÂä†newNodeËäÇÁÇπ
 * @param {Object} node 
 * @param {Object} newNode 
 */
dom.before = function (node, newNode) {
    let parent = node.parentNode;
    return parent.insertBefore(newNode, node);
}

/**
 * ÁªôÁà∂ËäÇÁÇπÊ∑ªÂä†Â≠êËäÇÁÇπ
 * @param {Object} parent Áà∂ËäÇÁÇπ
 * @param {Object} child Â≠êËäÇÁÇπ
 */
dom.append = function (parent, child) {
    parent.appendChild(child);
}

/**
 * Â∞ÜÁõÆÊ†áËäÇÁÇπÁöÑÁà∂ËäÇÁÇπÂèò‰∏∫parentËäÇÁÇπ
 * way === 1 : parentËäÇÁÇπÂª∫Á´ãÂú®ÁõÆÊ†áËäÇÁÇπÁà∂ËäÇÁÇπÁöÑÊúÄÂêéÈù¢
 * way === 2 : parentËäÇÁÇπ‰øùÊåÅÂú®ÁõÆÊ†áËäÇÁÇπÂéüÊù•ÁöÑ‰ΩçÁΩÆ
 * @param {Object} targetNode ÁõÆÊ†áËäÇÁÇπ
 * @param {Object} parent Áà∂ËäÇÁÇπ
 * @param {Number} way ÈÄâÊã©Ê∑ªÂä†ÊñπÂºè
 */
dom.wrap = function (targetNode, parent, way) {
    if (way === 1) {
        targetNode.parentNode.appendChild(parent);
        parent.appendChild(targetNode);
    } else if (way === 2) {
        dom.before(targetNode, parent);
        // ‰ΩøÁî®appendChildÂêéËäÇÁÇπ‰∏ç‰ºöËá™Â∑±Â§çÂà∂,ËÄåÊòØÁõ¥Êé•ÊîπËøáÂéª
        dom.append(parent, targetNode);
    }
}

/**
 * Âà†Èô§ÁõÆÊ†áËäÇÁÇπ
 * @param {Object} targetNode ÁõÆÊ†áËäÇÁÇπ
 * @returns {Object} ËøîÂõûÂà†Èô§ÁöÑÂ≠êËäÇÁÇπ
 */
dom.remove = function (targetNode) {
    let parent = targetNode.parentNode;
    return parent.removeChild(targetNode);
}

// Âà†Èô§Â≠êËäÇÁÇπ
dom.empty = function (targetNode) {
    let arr = new Array();
    let x = targetNode.firstChild;

    while (x) {
        arr.push(dom.remove(targetNode.firstChild));
        x = targetNode.firstChild;
    }
    return arr;
}

/**
 * Ëé∑ÂèñÊàñËÄÖËÆæÁΩÆËäÇÁÇπÁöÑÂ±ûÊÄßÂÄº
 * @param {Object} node ÈúÄË¶ÅÊìç‰ΩúÁöÑËäÇÁÇπ
 * @param {String} attrName Â±ûÊÄßÂêç
 * @param {String} attrValue Â±ûÊÄßÂÄº
 */
dom.attr = function (node, attrName, attrValue) {

    // ‰º†‰∫Ü‰∏§‰∏™ÂèÇÊï∞Â∞±ÊòØËØªÂ±ûÊÄß,‰∏â‰∏™ÂèÇÊï∞Â∞±ÊòØÂÜôÂ±ûÊÄß, ÈáçËΩΩ
    if (arguments.length === 2) {
        return node.getAttribute(attrName);
    } else if (arguments.length === 3) {
        node.setAttribute(attrName, attrValue);
    }
}

/**
 * ËØªÂÜôÊñáÊú¨ÂÜÖÂÆπ
 * @param {Object} node Êìç‰ΩúÁöÑËäÇÁÇπ
 * @param {String} textValue ÊñáÊú¨ÂÜÖÂÆπ
 */
dom.text = function (node, textValue) {

    if (arguments.length === 1) {
        return node.textContent;
    } else if (arguments.length === 2) {
        node.innerText = textValue;
    }
}

/**
 * ËØªÂÜôhtmlÂÜÖÂÆπ
 * @param {Object} node Êìç‰ΩúÂÜÖÂÆπ
 * @param {String} htmlValue htmlÂÜÖÂÆπ
 */
dom.html = function (node, htmlValue) {
    if (arguments.length === 1) {
        return node.innerHTML;
    } else if (arguments.length === 2) {
        node.innerHTML = htmlValue;
    }
}


/**
 * Â¢ûÂä†ËäÇÁÇπÁöÑÊ†∑Âºè / Ëé∑ÂèñËäÇÁÇπÁöÑÊ†∑Âºè
 * @param {Object} node 
 * @param {String/Object} name Â±ûÊÄßÂêçÊàñËÄÖÊòØ‰∏Ä‰∏™Â±ûÊÄßÂØπË±°{key:value}
 * @param {String} value Â±ûÊÄßÂÄº
 */
dom.style = function (node, name, value) {

    // style(test, `color`, `red`)
    if (arguments.length === 3) {
        node.style[name] = value;
    } else if (arguments.length === 2) {

        // Â¶ÇÊûúnameÊòØÂ≠óÁ¨¶‰∏≤Â∞±ËøîÂõûvalue
        if (typeof name === 'string') {
            return node.style[name];
        } else if (name instanceof Object) {
            const obj = name;
            for (key in obj) {
                console.log(`obj:${obj}`);
                console.log(`key:${key}, value:${obj[key]}`);
                node.style[key] = obj[key];
            }
        }

    }
}

// ÈáçÂÜôËäÇÁÇπÊ†∑Âºè
/**
 * ÂíåÂ¢ûÂä†ËäÇÁÇπÊ†∑ÂºèÁöÑÂ∑Æ‰∏çÂ§ö, Âå∫Âà´ÊòØ 1.‰∏çËÉΩËé∑ÂèñÊ†∑Âºè 2. ÊØè‰∏ÄÊ¨°Ë∞ÉÁî®ÈÉΩÈáçÊñ∞ÂÜôÂÖ•Ê†∑Âºè
 * @param {Object} node 
 * @param {Object/String} name 
 * @param {Object} value 
 */
dom.reStyle = function (node, name, value) {

    // Ê∏ÖÁ©∫Ê†∑Âºè
    node.style = '';

    if (arguments.length === 3) {
        node.style = `${name}:${value};`;
    } else if (arguments.length === 2) {
        const obj = name;
        for (key in obj) {
            node.style[key] = obj[key];
        }
    }
}

dom.class = {

    // Â¢ûÂä†ËäÇÁÇπclass
    add(node, className) {
        node.classList.add(className);
    },

    // Âà†Èô§ËäÇÁÇπclass
    remove(node, className) {
        node.classList.remove(className);
    },

    // Âà§Êñ≠ËäÇÁÇπÊòØÂê¶Êúâclass
    has(node, className) {
        node.classList.has(className);
    },
    // ÊõøÊç¢
    replace(node, oldName, newName) {
        node.classList.replace(oldName, newName);
    }

};


dom.on = function (node, eventName, func) {
    node.addEventListener(eventName, func)
}

dom.off = function (node, eventName, func) {
    node.removeEventListener(eventName, func);
}

// ÈÄöËøáÈÄâÊã©Âô®,ËøîÂõû‰∏Ä‰∏™node
dom.find = function (selector, scope) {
    return (scope || document).querySelectorAll(selector);
}

// ËøîÂõûÁà∂ÂÖÉÁ¥†
dom.parent = function (node) {
    return node.parentNode;
}

// ËøîÂõûÂ≠êÂÖÉÁ¥†, ÂÖÉÁ¥†ËäÇÁÇπ
dom.children = function (node) {
    return node.children;
}

// ËøîÂõûÂÖÑÂºüÂÖÉÁ¥†,
dom.sibling = function (node) {
    return Array.from(node.parentNode.children).filter(n => n != node);
}

dom.next = function (node) {
    let x = node.nextSibling;
    while (x && x.nodeType === 3) {
        x = x.nextSibling;
    }
    return x;
}

dom.previous = function (node) {
    let x = node.previousSibling;
    while (x && x.nodeType === 3) {
        x = x.previousSibling;
    }
    return x;
}

dom.each = function (nodeList, fn) {

    for (let i = 0; i < nodeList.length; i++) {
        fn.call(null, nodeList[i]);
    }

}

dom.index = function (node) {
    const list = dom.children(node.parentNode);
    let i;
    for (i = 0; i < list; i++) {
        if (node === list[i]) {
            break;
        }
    }

    return i;
}